@include defineVariable(--icon-01, getColor(neutral, 100));
@include defineVariable(--icon-02, getColor(neutral, 70));


@include defineVariable(--hover-ui, getColor(neutral, 10));
@include defineVariable(--active-ui, getColor(neutral, 30));


@include defineVariable(--danger-01, getColor(red, 60));

@include defineVariable(--danger-02, getColor(red, 60));

@include defineVariable(--support-01, getColor(error, 60));
